





















































































export default {
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			currentTab: 0,
			menuTabs: [{
				name: 'Crime'
			}, {
				name: 'Drama'
			}, {
				name: 'Comedy'
			}, {
				name: 'Sci-Fi'
			}, {
				name: 'Animation'
			}, {
				name: 'Action'
			}],

			swiperDateList: [
				[],
				[],
				[],
				[],
				[],
				[]
			],
			// 								indicatorDots: true,
			// 								autoplay: true,
			// 								interval: 5000,
			// 								duration: 500,
			// 								circular: true

			// 用于分页的属性
			totalPage: 1,
			page: 1,
			videoList: [],

			screenWidth: 350,
			serverUrl: "",

		}
	},

	changeIndicatorDots(e) {
		this.indicatorDots = !this.indicatorDots
	},
	changeAutoplay(e) {
		this.autoplay = !this.autoplay
	},
	intervalChange(e) {
		this.interval = e.target.value
	},
	durationChange(e) {
		this.duration = e.target.value
	},

	onLoad() {
		uni.setNavigationBarTitle({
			title: 'TheShortFilmShow'
		});

		var screenWidth = uni.getSystemInfoSync().screenWidth;
		this.screenWidth = screenWidth;

		// 获取当前页面
		var page = this.page;
		this.getAllVideoList(page);

	},

	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},

	methods: {
		swichMenu: async function(current) { //点击其中一个 menu
			if (this.currentTab == current) {
				return false;
			} else {
				this.currentTab = current;
				this.setScrollLeft(current);
			}
		},
		swiperChange: async function(e) {
			let index = e.target.current;
			this.setScrollLeft(index);
			this.currentTab = index;
		},
		setScrollLeft: async function(tabIndex) {
			let leftWidthSum = 0;
			for (var i = 0; i <= tabIndex; i++) {
				let nowElement = await this.getWidth('tabNum' + i);
				leftWidthSum = leftWidthSum + nowElement.width;
			}
			let winWidth = uni.getSystemInfoSync().windowWidth;
			this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
		},
		getWidth: function(id) { //得到元素的宽高
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		loadMore: function(tabIndex) {
			console.log('正在加载更多数据。。。')
			this.getDateList(tabIndex);
		},
		getDateList: function(tabIndex) {
			for (var i = 0; i < 20; i++) {
				var entity = this.menuTabs[tabIndex].name + (this.swiperDateList[tabIndex].length);
				this.swiperDateList[tabIndex].push(entity);
			}
		},

		getAllVideoList: function(page) {
			var that = this;
			var serverUrl = that.$serverUrl;
			uni.showLoading({
				title: 'Loading...',
			});
			uni.request({
				url: serverUrl + '/video/showAll?page=' + page,
				method: "POST",
				success: (res) => {
					uni.hideLoading();
					console.log(res.data.data);
					// 如果page 是第一页 清空videoList
					if (page == 1) {
						that.videoList = [];
					}

					var videoList = that.videoList;
					var newVideoList = res.data.data.rows;

					that.videoList = videoList.concat(newVideoList);
					that.page = page;
					that.totalPage = res.data.data.total;
					that.serverUrl = serverUrl;
				}
			});
		},

		getMoreAllVideoList: function() {
			var currentPage = this.page;
			var totalPage = this.totalPage;

			if (currentPage != totalPage) {
				var page = currentPage + 1;
				this.getAllVideoList(page);
				this.page = page;
			}

		},

		navigatToVideoPlay: function(film) {
			console.log(film);
			film = JSON.stringify(film)
			uni.navigateTo({
				url: '../videoplay/videoplay?film=' + film
			});
		}

	}

}
